<template>
    <div class="edit container">
        <h1 class="page-header">编辑用户</h1>
        <form v-on:submit="addCustomer">
           <div class="well">
               <h4>用户信息</h4>
               <div class="form-group">
                   <label for="exampleInputEmail1">姓名</label>
                   <input type="text" class="form-control" id="exampleInputEmail1" placeholder="name" v-model="customer.name" required>
               </div>
               <div class="form-group">
                   <label for="exampleInputEmail1">电话</label>
                   <input type='tel' class="form-control" id="exampleInputEmail1" placeholder="tel" v-model="customer.phone" required>
               </div>
               <div class="form-group">
                   <label for="exampleInputEmail1">邮箱</label>
                   <input type="email" class="form-control" id="exampleInputEmail1" placeholder="email" v-model="customer.email" required>
               </div>
               <div class="form-group">
                   <label for="exampleInputEmail1">学历</label>
                   <input type="text" class="form-control" id="exampleInputEmail1" placeholder="education" v-model="customer.education">
               </div>
               <div class="form-group">
                   <label for="exampleInputEmail1">毕业学校</label>
                   <input type="text" class="form-control" id="exampleInputEmail1" placeholder="graduationschool" v-model="customer.graduationschool">
               </div>
               <div class="form-group">
                   <label for="exampleInputEmail1">职业</label>
                   <input type="text" class="form-control" id="exampleInputEmail1" placeholder="profession" v-model="customer.profession">
               </div>
               <div class="form-group">
                   <label for="exampleInputEmail1">个人简介</label>
                   <textarea  class="form-control" rows="10" v-model="customer.profile"></textarea>
               </div>

               <button type="submit" class="btn btn-default">Submit</button>
           </div>

        </form>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "edit",
        data(){
            return{
                customer:{
                }
            }
        },
        methods:{
            addCustomer:function(e){
                e.preventDefault();
                axios.put("http://localhost:3000/users/"+this.$route.params.id,this.customer).then(()=>{
                    // console.log(data);
                    this.$router.push({path:'/',query:{alert:"用户信息修改成功！"}});
                })
            },
            fetchData:function (id) {
                axios.get("http://localhost:3000/users/"+id).then(response=>{
                        // console.log(response);
                        this.customer= response.data;
                    }
                )
            }
        },
        created() {
            this.fetchData(this.$route.params.id);
        }

    }
</script>

