<template>
    <div class="detail container">
        <router-link to="/" class="btn btn-default">返回</router-link>
        <h1 class="page-header">{{customer.name}}
        <span class="pull-right">
            <router-link class="btn btn-primary" :to="'/edit/'+customer.id">编辑</router-link>
            &nbsp;
            <button class="btn btn-danger" v-on:click="deleteCustomer(customer.id)">删除</button>
        </span>
        </h1>
        <ul class="list-group">
            <li class="list-group-item"><span class="glyphicon glyphicon-asterisk">{{customer.phone}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-asterisk">{{customer.email}}</span></li>
        </ul>
        <ul class="list-group">
            <li class="list-group-item"><span class="glyphicon glyphicon-heart">{{customer.education}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-heart">{{customer.graduationschool}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-heart">{{customer.profession}}</span></li>
            <li class="list-group-item"><span class="glyphicon glyphicon-heart">{{customer.profile}}</span></li>
        </ul>
    </div>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "CustomersDetail",
        data(){
            return{
                customer:{},
            }
        },
        methods:{
            fetchData:function (id) {
                axios.get("http://localhost:3000/users/"+id).then(response=>{
                        // console.log(response);
                    this.customer= response.data;
                    }
                )
            },
            deleteCustomer(id){
                axios.delete("http://localhost:3000/users/"+id).then(()=>{
                    this.$router.push({path:'/',query:{alert:"用户信息删除成功！"}});
                })
            }
        },
        created() {
            this.fetchData(this.$route.params.id);
        }
    }
</script>

