<template>
  <div class="customers container">
   <alert-mes v-if='alert' v-bind:message="alert"></alert-mes>
   <h1 class="page-header">用户管理系统</h1>
    <input type="text" class="form-control" placeholder="搜素" v-model="filterInput">
    <table class="table table-striped">
      <thead>
      <tr>
        <th>姓名</th>
        <th>电话</th>
        <th>邮箱</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="customer in filterData(customers,filterInput)" :key="customer.id">
        <th>{{customer.name}}</th>
        <th>{{customer.phone}}</th>
        <th>{{customer.email}}</th>
        <th><router-link :to="'/customers/'+customer.id" class="btn btn-default">详情</router-link></th>
      </tr>
      </tbody>

    </table>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import AlertMes from '../views/alertMes'
export default {
  name: 'customers',
  components:{
    alertMes:AlertMes,
  },
  data(){
    return {
      customers:[],
      alert:'',
      filterInput:'',
    }
  },
  methods:{
    fetchData() {
      axios.get("http://localhost:3000/users").then(response=>{
        this.customers = response.data;
        // console.log(response.data);
      })
    },
    filterData(customers,value){
      return customers.filter(customer=>{
        return customer.name.match(value);
      })
    }
  },
 created(){
    if(this.$route.query.alert){
      this.alert = this.$route.query.alert;
    }
    this.fetchData();
 },
  updated() {
    this.fetchData();
  }

}
</script>
